// ---    Business structure     ---  //

.business-structure {
  font-size: 1rem;
  position: relative;
  margin-bottom: 13em;

  &::before {
    content: '';
    position: absolute;
    bottom: -10.3em;
    right: 0;
    height: 9.3em;
    width: 105.2em;
    background-image: url(../images/business/bg__text--business-structure.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    z-index: -1;
  }

  .article {
    position: relative;
    display: flex;

    &__photo {
      display: block;
      background-image: url(../images/business/photo__business--structure.jpg);
      background-repeat: no-repeat;
      background-position: right top;
      background-size: 55.8em 83.7em;
      width: 100%;
      height: auto;
      padding: 9em 0 8.5em 0;
    }


    &__text-block {
      position: relative;
      width: 67.6em;
      background: $rc-gradient-button;
      padding: 6em 2.5em 4em 5em;
      margin-right: auto;
      margin-left: 0;
      margin-bottom: 0;

      .title {
        font-family: $rc-font-family-title;
        font-size: $rc-font-size-h2;
        line-height: 1em;
        margin-top: 0;
        margin-bottom: 0.5em;
      }

      .text {
        font-family: $rc-font-family-accent;
        display: block;
        font-size: $rc-font-size-h5;
        font-weight: 400;
        line-height: 1.5;
        margin-top: 0;
        margin-bottom: 0.5em;
      }
    }
  }
}
