// ---    Home     ---  //

.home {
  position: relative;
  font-size: 1rem;
  display: flex;
  padding-bottom: 4.5em;

  &::before {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    width: 192em;
    height: 100em;
    background-image: url(../images/promo/banner__home.jpg);
    background-repeat: no-repeat;
    background-position: center right;
    background-size: contain;
    z-index: -1;
  }

  &__box {
    position: relative;
  }

  .socials {
    position: absolute;
    top: 17em;
    left: -37.5em;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-color: $rc-bg--black;
    width: 18.8em;
    height: 60em;

    &::before {
      content: '';
      position: absolute;
      top: -5.1em;
      left: 0;
      height: 5.1em;
      width: 100%;
      background-color: $rc-bg--black;
    }

    &::after {
      content: '';
      position: absolute;
      top: -8em;
      left: 0;
      height: 15em;
      width: 49em;
      background-image: url(../images/bg__text--home.png);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      z-index: 0;
    }

    &__label {
      position: absolute;
      top: 3em;
      left: -8.8em;
      width: 39.7em;
      font-size: .88rem;
      letter-spacing: .8em;
      white-space: nowrap;
      text-transform: uppercase;
      transform: rotate(-90deg);
      padding-left: 3.4em;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: -6.25em;
        width: 6.25em;
        height: 0.11em;
        background-color: #555555;
        margin: auto;
      }
    }

    &__link {
      display: block;
      width: 2.6em;
      height: 2.4em;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      margin: 3em 8.4em 1.3em;
    }

    .icon-instagram {
      background-image: url(../images/icons/instagram.png);
    }

    .icon-vk {
      background-image: url(../images/icons/vk.png);
    }

    .icon-facebook {
      background-image: url(../images/icons/facebook.png);
    }

    .icon-ok {
      background-image: url(../images/icons/ok.png);
      margin-bottom: 4.3em;
    }
  }

  .top-carousel {

    &__container {
      width: 118em;
      height: 100%;
    }

    &__wrapper {
      padding-right: 4em;
      margin-left: -4em;
    }

    &__navigation {
      position: absolute;
      display: flex;
      top: 19em;
      left: -22em;
    }

    .top-offer {
      text-align: right;
      margin: 16em auto 28em;

      &__title {
        font-family: $rc-font-family-title;
        font-size: $rc-font-size-h1;
        line-height: $rc-line-height-h1;
        font-weight: 400;
        letter-spacing: 0.125em;
        margin-top: 0;
        margin-bottom: 0.8em;
      }

      &__subtitle {
        font-size: 2.9rem;
        font-weight: 400;
        margin-top: 0;
        margin-bottom: 2.13em;
      }
    }
  }
}
