// ---    Contacts     ---  //

.home-contacts {
  position: relative;
  font-size: 1rem;
  margin-bottom: 7.5em;

  &::before {
    content: '';
    position: absolute;
    top: -2em;
    right: 0;
    height: 7em;
    width: 48.3em;
    background-image: url(../images/bg__text--home-contacts.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    z-index: 0;
  }

  &__content {
    position: relative;
    display: flex;
  }

  &__address {
    font-size: $rc-font-size-footer;
    width: 34em;
    margin-top: 0;
    margin-bottom: 1em;
    margin-right: 2em;
  }

  &__form-full {

    .form {

      .top {
        display: flex;

        .left {
          margin-right: 1em;
        }

        .field {
          margin-bottom: 1em;
        }
      }

      .bottom {
        display: flex;

        .button-gr {
          margin-right: 0;
          margin-left: auto;
        }
      }


    }
  }
}
