// ---    Home     ---  //

.home-about {
  font-size: 1rem;
  position: relative;
  margin: 7em 0;

  &::before {
    content: '';
    position: absolute;
    top: 14em;
    left: 0;
    height: 6.9em;
    width: 64.6em;
    background-image: url(../images/bg__text--home-about.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    z-index: 0;
  }

  &__title {
    margin-bottom: 1.96em;
  }

  &__content {
    display: flex;
    flex-direction: row;

    > * {
      flex: 0 1 calc(33% - 1.5em);
      width: 100%;
      margin-right: 1.5em;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__description {

    .title {
      font-size: $rc-font-size-h3;
      margin: 0 0 0.8em;
    }

    .text {
      font-size: $rc-font-size-h5;
      line-height: 1.25em;
    }
  }

  &__types {

    .title {
      font-size: $rc-font-size-h3;
      margin: 0 0 0.8em;
    }
  }

  .types {

    &__list {
      list-style: none;
      padding: 0;
    }

    &__item {
      display: flex;
      align-items: center;
      font-size: 1.5rem;
      text-transform: uppercase;
      margin-bottom: 1.66em;

      &:last-child {
        margin-bottom: 0;
      }

      .icon {
        width: 5em;
        height: 5.6em;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        margin-left: 2em;
        margin-right: 2.66em;
      }

      .icon-petroleum {
        background-image: url(../images/icons/petroleum.png);
      }

      .icon-gaz {
        background-image: url(../images/icons/gaz.png);
      }

      .icon-petrochemical {
        background-image: url(../images/icons/petrochemical.png);
      }

      .icon-oil {
        background-image: url(../images/icons/oil.png);
      }
    }
  }

  &__photo {
    padding-top: 0.5em;

    .img {
      width: 39.6em;
      height: auto;
    }
  }
}
