// ---    Breadcrumbs     ---  //

.breadcrumbs {
  font-size: 1rem;
  position: relative;
  height: 12.2em;

  &__nav {
    height: 100%;
  }

  &__list {
    display: flex;
    align-items: center;
    height: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__item {
    position: relative;
    margin-left: 2.5em;

    &:first-child {
      margin-left: 0;
    }

    &:not(:first-child):before {
      content: '/';
      position: absolute;
      left: -1em;
      font-size: $rc-font-size-h6;
    }
  }

  &__link {
    font-size: $rc-font-size-h6;
    color: $rc-white;

    &.link {
      color: $rc-accent;
    }

    &.active {
      pointer-events: none;
      cursor: default;
      text-decoration: none;
    }

    &:not(.active) {
      text-decoration: underline;
    }
  }

}
