// ---    Parthers     ---  //

.home-partners {
  font-size: 1rem;
  position: relative;
  text-align: center;
  margin-top: 12.8em;
  margin-bottom: 16em;

  &::before {
    content: '';
    position: absolute;
    top: 39em;
    left: 0;
    height: 8.9em;
    width: 87.1em;
    background-image: url(../images/bg__text--home-partners.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    z-index: 0;
  }

  &__ten {
    display: flex;
    align-items: flex-end;
    justify-content: center;

    .img {
      width: 28.3em;
      height: 33.6em;
    }

    .title-h2 {
      font-size: 4rem;
      text-align: left;
      margin-left: 1em;
      margin-bottom: 1.5em;
    }
  }

  &__suppliers,
  &__buyers {
    padding-top: 5.7em;
    padding-bottom: 6em;

    .title-h2 {
      margin-bottom: 0.88em;
    }

    .brand-slider {

      &__container {
        width: 100%;
        height: 10em;
      }

      &__slide {

        .brand-item {
          display: block;
          width: 100%;
          height: 100%;
          background-repeat: no-repeat;
          background-position: center center;
          background-size: contain;
        }

        &:nth-child(1) {

          .brand-item {
            background-image: url(../images/brands/rosneft.png);
          }
        }

        &:nth-child(2) {

          .brand-item {
            background-image: url(../images/brands/gazprom.png);
          }
        }

        &:nth-child(3) {

          .brand-item {
            background-image: url(../images/brands/solid.png);
          }
        }

        &:nth-child(4) {

          .brand-item {
            background-image: url(../images/brands/spbmtsb.png);
          }
        }

        &:nth-child(5) {

          .brand-item {
            background-image: url(../images/brands/m7-soft.png);
          }
        }
      }
    }
  }

  &__buyers {
    padding-top: 0;
    padding-bottom: 10em;

    .brand-slider {

      &__slide {

        &:nth-child(1) {

          .brand-item {
            background-image: url(../images/partners/ch.png);
          }
        }

        &:nth-child(2) {

          .brand-item {
            background-image: url(../images/partners/energoplus.png);
          }
        }

        &:nth-child(3) {

          .brand-item {
            background-image: url(../images/partners/morskoy-trast.png);
          }
        }

        &:nth-child(4) {

          .brand-item {
            background-image: url(../images/partners/tngs.png);
          }
        }

        &:nth-child(5) {

          .brand-item {
            background-image: url(../images/partners/toplivo-snabjenie.png);
          }
        }
      }
    }
  }
}
