@import "variables";

@mixin font($family, $fstyle, $fweight, $folder, $fname) {

    @font-face {
        font-family: $family;
        font-style: $fstyle;
        font-weight: $fweight;
        font-display: swap;
        src:
            url("/#{$folder}/#{$fname}.woff2") format("woff2"),
            url("/#{$folder}/#{$fname}.woff") format("woff"); // Fix IE 9-11
    }
}

@mixin translate($x, $y) {
    -webkit-transform: translate($x, $y);
    -moz-transform: translate($x, $y);
    -ms-transform: translateX($x) translateY($y);
    -o-transform: translate($x, $y);
    transform: translate($x, $y);
    -webkit-backface-visibility: hidden;
}

@mixin transition($transitions...) {
    -webkit-transition: $transitions;
    -moz-transition: $transitions;
    -o-transition: $transitions;
    transition: $transitions;
}

@mixin absolute-center() {
    position: absolute;
    left: 50%;
    top: 50%;
    bottom: auto;
    right: auto;

    @include translate(-50%, -50%);
}


@mixin box($width,$height: $width) {
    width: $width;
    height: $height;
  }
  @mixin cover-background {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  $units: 1px !default;
  @mixin screen($res-first, $res-last: $res-first) {
    @if $res-first == $res-last {
      @media screen and (max-width: if(unitless($res-first), ($res-first - 1) *$units, $res-first)) {
        @content;
      }
    }
    @else if $res-first < $res-last {
      @media screen and (min-width: if(unitless($res-first), $res-first*$units, $res-first)) and (max-width: if(unitless($res-last), $res-last*$units, $res-last)) {
        @content;
      }
    }
  }
  @mixin min-screen($res) {
    @media screen and (min-width: if(unitless($res), $res*$units, $res)) {
      @content;
    }
  }

@mixin media-mobile {

    @media (max-width: $rc-mediaWidthMobile) {
        @content;
    }
}

@mixin media-tablet {

    @media (max-width: $rc-mediaWidthTablet) {
        @content;
    }
}

@mixin media-tablet-horizontal {

    @media (max-width: $rc-mediaWidthTabletHorizontal) {
        @content;
    }
}

@mixin media-laptop {

    @media (max-width: $rc-mediaWidthLaptop) {
        @content;
    }
}

@mixin media-desctop {

    @media (max-width: $rc-mediaWidthDesctop) {
        @content;
    }
}

@mixin media-custom($width) {

    @media (max-width: $width) {
        @content;
    }
}

@mixin background($imgpath,$position:center center,$repeat: no-repeat,$size: cover) {
    background: {
        image: url($imgpath);
        position: $position;
        repeat: $repeat;
        size: $size;
    }
}
