// ---    Header     ---  //

.header {
  font-size: 1rem;
  position: relative;
  background-color: $rc-bg--black;
  padding-bottom: 5em;

    &__box {
      position: relative;
    }

    &__top {
      position: relative;
      display: flex;
      align-items: flex-end;
      padding-top: 2.5em;
      padding-bottom: 5em;
    }
}

// ---    Logo     ---  //

.logo {
  font-size: 1rem;
  position: absolute;
  top: 0;
  left: -37.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $rc-bg--black;
  width: 37.5em;
  height: 40em;

  &__link {
    display: block;
    width: 14.5em;
    height: 18.5em;
    margin-bottom: 1.5em;
  }

  &__img {
    width: 100%;
    height: 100%;
  }
}

// ---    Search     ---  //

.search {
  position: relative;
  display: block;
  width: 30em;
  height: 4.6em;
  margin-bottom: 1em;

  &__field {
    font-family: $rc-font-family-accent;
    font-size: $rc-font-size-h6;
    color: $rc-white;
    background-color: transparent;
    border: 0.14em solid $rc-border--input;
    border-radius: 0.35em;
    width: 100%;
    height: 100%;
    padding: 1.15em 6.5em 1.07em 3.9em;

    &::placeholder {
      font-family: $rc-font-family-accent;
      font-size: $rc-font-size-h6;
      text-transform: uppercase;
      color: $rc-white;
    }
  }

  &__select {
    position: absolute;
    display: block;
    right: 4.6em;
    top: 0;
    bottom: 0;
    width: 4.6em;
    height: 100%;
    border: 0;
    background-color: transparent;
    background-image: url(../images/icons/arrow--down.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 2.6em 1.5em;
    margin: auto 0 auto 0.3em;
    cursor: pointer;
  }

  &__button {
    position: absolute;
    display: block;
    right: 0;
    top: 0;
    bottom: 0;
    width: 4.6em;
    height: 100%;
    border: 0;
    background-color: transparent;
    background-image: url(../images/icons/zoom.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 2.3em 2.4em;
    margin: auto 0 auto 0.3em;
    cursor: pointer;
  }
}

// ---    Run Stroke     ---  //

.run-stroke {
  display: flex;
  align-items: center;
  white-space: nowrap;
  overflow: hidden;
  max-width: 45.7em;
  height: 2.4em;
  border-left: .2em solid $rc-run---stroke;
  padding: 0 1.2em;
  margin-left: 3.6em;
  margin-bottom: 2em;

  &__text {
    display: inline-block;
    font-size: $rc-font-size-footer;
    letter-spacing: 0.075em;
    color: $rc-run---stroke;
    text-transform: uppercase;
    padding-left: 100%;
    animation:scroll 15s infinite linear;
  }

  @-webkit-keyframes scroll {

    0% {
      -webkit-transform: translate(0,0);
      transform: translate(0,0)
    }

    100% {
      -webkit-transform: translate(-100%,0);
      transform: translate(-100%,0)
    }
  }

  @-moz-keyframes scroll {

    0% {
      -moz-transform: translate(0,0);
      transform: translate(0,0)
    }

    100% {
      -moz-transform: translate(-100%,0);
      transform: translate(-100%,0)
    }
  }

  @keyframes scroll {

    0% {
      transform: translate(0,0)
    }

    100% {
      transform: translate(-100%,0)
    }
  }
}

// ---    Contact     ---  //

.contact {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-left: 9em;

  &__phone {
    font-size: $rc-font-size-phone;
    line-height: 1em;
    color: $rc-white;
  }

  &__call {
    margin-top: 1.5em;
  }
}



// ---    Menu Top     ---  //

.menu-top {
  position: relative;
  display: block;

  &__list {
    display: flex;
    justify-content: flex-end;
    list-style-type: none;
    padding: 0;
  }

  &__item {
    margin-left: 10em;

    &:nth-child(1) {
      margin-left: 0;
    }

    &:hover,
    &.active {

      a {
        color: #70569e;
      }
    }
  }

  &__link {
    font-family: $rc-font-family-title;
    font-size: $rc-font-size-menu;
    letter-spacing: 0.08em;
  }
}
