// ---    Map-delivery     ---  //

.home-map-delivery {
  position: relative;
  font-size: 1rem;
  text-align: center;
  margin-bottom: 20em;

  &::before {
    content: '';
    position: absolute;
    top: 5em;
    right: 0;
    height: 16.6em;
    width: 50em;
    background-image: url(../images/bg__text--home-map-delivery.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    z-index: 0;
  }

  &__title {
    margin-bottom: 1.66em;
  }

  &__content {
    margin-bottom: 1em;

    .interactive-map {
      font-size: 1rem;
      position: relative;
      display: flex;
      align-items: center;
      height: 71em;

      .icon-map-delivery {
        position: absolute;
        left: 50%;
        fill: #606060;
        transform: translateX(-50%);
        display: inline-block;
        width: 136em;
        height: 71em;
      }

      .dot-glow {
        position: absolute;
        width: 14.3em;
        height: 14.3em;
        background-image: url(../images/icons/dot-glow.png);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        cursor: pointer;
      }

      .icon-dotMoskow {
        top: 38.9em;
        left: 3.8em;
      }

      .icon-dotMoskow2 {
        top: 36.8em;
        left: 26.3em;;
      }

      .icon-dotMoskow3 {
        bottom: 6.5em;
        left: 42em;
      }

      .icon-dotMoskow4 {
        top: 14.2em;
        left: 42em;
      }

      .icon-dotMoskow5 {
        top: 36.8em;
        left: 57.8em;
      }

      .icon-dotMoskow6 {
        bottom: 6.5em;
        right: 10.6em;
      }

      .icon-dotMoskow7 {
        top: 12em;
        right: 6em;
      }

      .tooltip {
        text-align: left;
        transition: all 0.3s ease-in-out;

        &__text  {
          font-size: 1rem;
          visibility: hidden;
          z-index: 1;
          opacity: 0.40;

          width: 25.3em;
          height: 25.3em;
          padding: 2.4em 1.5em;

          background: rgba($color: #000000, $alpha: 0.6);
          color: $rc-white;

          position: absolute;
          top:-120%;
          right: -165%;


          border-radius: .2em;

          transform: translateY(.9em);
          transition: all 0.3s ease-in-out;
        }
        &:hover {

          .tooltip__text {
            visibility: visible;
            transform: translateY(-10px);
            opacity: 1;
            transition: .3s linear;
            animation: odsoky 1s ease-in-out infinite  alternate;
          }
        }

        .city {
          font-size: 2.2rem;
          line-height: 1em;
          font-weight: 700;
          color: $rc-accent;
          border-bottom: 0.04em solid #606060;
          text-transform: uppercase;
          padding-bottom: 0.8em;
          margin-top: 0;
          margin-bottom: 1.27em;
        }

        .name,
        .address,
        .phone,
        .time {
          font-size: 1.7rem;
          line-height: 1em;
        }
      }
      @keyframes odsoky {
        0%{
          transform: translateY(.6em);
        }

        100%{
          transform: translateY(.1em);
        }

      }
    }
  }
}
