@import "../helpers/mixins";
@import "../helpers/variables";

* {
	box-sizing: border-box;
	word-wrap: break-word;
}

//------------------------------------------------------------base styles
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);
}

html {
	height: 100%;
	font: 400 calc(100vw / 1920 * 10)/1.33 $rc-font-family-default;
}

body {
	position: relative;

	display: flex;
	height: 100%;

	font-size: $rc-font-size-default;
	color: $rc-white;

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: $rc-text--black;

	@supports (display: grid) {
		display: block;
	}

}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.box  {
  margin: 0 auto;
	max-width: 117rem;
	height: 100%;
  width: 100%;
	padding: 0 1.5em 0 1.5em;

	&--fluid {
		max-width: 100%;
		padding: 0;
	}
}

.base {
	width: 100%;
	position: relative;
	padding: 0 0 0 0;

	flex: 1 0 auto;
}

a {
	text-decoration: none;
	color: $rc-white;

	&:hover,
	&:focus {
		text-decoration: none;
	}

}

.elements_list {
	padding: 3rem;
}

.button {

  &:hover {
    text-decoration: none;
  }

}

.title-h2 {
	font-family: $rc-font-family-title;
	font-size: $rc-font-size-h2;
	line-height: 1em;
	letter-spacing: 0.125em;
	text-align: center;

	.color-accent {
		color: $rc-accent;
	}
}

.img {
	display: block;
	width: 100%;
	height: auto;
}


