// ---    Services     ---  //

.home-services {
  font-size: 1rem;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 6.5em;
    left: 0;
    height: 6.9em;
    width: 64.6em;
    background-image: url(../images/bg__text--home-services.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    z-index: 0;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 1.37em;
  }

  .services-slider {

    &__container {
      width: 100%;
      height: 100%;
    }

    &__button-next,
    &__button-prev {
      position: absolute;
      display: flex;
      height: 0.9em;
      top: 0;
      bottom: 0;
      margin: auto 5em;
      z-index: 1;
    }

    &__button-next {
      right: 0;
      background-image: url(../images/icons/arrow__slider--right-white.png);
    }

    &__button-prev {
      left: 0;
      background-image: url(../images/icons/arrow__slider--left-white.png);
    }

    &__cards {
      background-image: url(../images/services__cards.jpg);

      &::before {
        background-color: #334242;
      }
    }

    &__pipe {
      background-image: url(../images/services__pipe.jpg);

      &::before {
        background-color: #3c4778;
      }
    }

    &__delivery {
      background-image: url(../images/services__delivery.jpg);

      &::before {
        background-color: #273e53;
      }
    }

    &__capacity {
      background-image: url(../images/services__capacity.jpg);

      &::before {
        background-color: #43334a;
      }
    }

    .services-item {
      position: relative;
      display: flex;
      flex-direction: column;
      font-family: $rc-font-family-title;
      width: 48.2em;
      height: 88.3em;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      padding: 5.5em 3.5em;
      cursor: pointer;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: all .3s ease-in-out;
        opacity: 0.8;
        z-index: 0;
      }

      &.active {

        &::before {
          opacity: 0;
          z-index: -1;
        }

        .services-item__zoom {
          position: absolute;
          display: flex;
          align-items: center;
          justify-content: center;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          border-radius: 100em;
          width: 14.7em;
          height: 14.7em;
          background-image: url(../images/icons/zoom--plus-bg.png);
          background-repeat: no-repeat;
          background-position: center center;
          background-size: contain;
          margin: auto;

          .icon-zoom {
            display: block;
            width: 4.2em;
            height: 4.2em;
            background-image: url(../images/icons/zoom--plus.png);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: contain;
          }
        }
      }

      &__title {
        position: relative;
        font-size: $rc-font-size-h3;
        font-weight: 400;
        letter-spacing: .015em;
      }

      &__naming {
        position: relative;
        font-size: 1.5rem;
        font-weight: 400;
        letter-spacing: .523em;
        transform: rotate(-180deg);
        writing-mode: vertical-rl;
        margin-top: auto;
        margin-bottom: 0;
      }
    }
  }
}
