// Constants
$rc-button--bg: $rc-accent;
$rc-button--bg-shadow: rgba(0, 0, 0, 0.2);
$rc-button--color: $rc-white;
$rc-button--fz: 1rem;
$rc-button--fz-sm: 1.4rem;
$rc-button--fz-lg: 1.8rem;
$rc-button--pd: 1.7em 1em;

.button-gr,
.button {
    display: inline-block;
    border: 1px solid $rc-border--button;
    border-radius: 6em;
    padding: $rc-button--pd;
    margin: 0;
    text-decoration: none;
    font-size: $rc-button--fz;
    cursor: pointer;
    text-align: center;

    .submit__text,
    &__text {
      font-size: 1.7rem;
      text-transform: uppercase;
      color: $rc-white;
    }

    &--light {
      width: 30em;
      height: 6em;
      background: transparent;
    }

    &--detail {
      font-size: 1.6rem;
      color: #504f4f;
      background-image: url(../images/icons/arrow__quotes.png);
      background-repeat: no-repeat;
      background-position: center right;
      border: 0;
      padding: 0 1.25em 0 0;
    }

    &--gradient {
      border: 0;
      background: $rc-gradient-button;
      box-shadow: 0 0 2em $rc-button--bg-shadow;
      padding-left: 3.5em;
      padding-right: 3.5em;

      &:hover {
        background: $rc-gradient-button;
      }
    }

    &--arrow {

      .button-gr__text {
        background-image: url(../images/icons/arrow__slider--right-white.png);
        background-repeat: no-repeat;
        background-position: center right;
        background-size: 1em 0.7em;
        padding-right: 1.75em;
      }
    }

    &--small {
      font-size: $rc-button--fz-sm;
    }

    &--large {
      font-size: $rc-button--fz-lg;
    }

    &--rc-custom {
      box-shadow: 4px 4px 0 $rc-button--bg-shadow;
      transition: all .2s ease-in-out;

        &:hover {
          box-shadow: 1px 1px 0 $rc-button--bg-shadow;
        }
    }
}

.button:hover,
.button:focus {
  transition: all .2s ease-in-out;
}

.button:focus {
  outline: 1px solid $rc-button--color;
  outline-offset: -4px;
}

.button:active {
  transform: scale(0.99);
}

.slider-button-next,
.slider-button-prev {
  font-family: $rc-font-family-title;
  font-size: $rc-font-size-h5;
  line-height: 1em;
  letter-spacing: .115em;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 0 2.18em;
  cursor: pointer;
}

.slider-button-prev {
  background-image: url(../images/icons/arrow__slider--right.png);
  background-position: center right;
  margin-left: 1.8em;

}

.slider-button-next {
  background-image: url(../images/icons/arrow__slider--left.png);
  background-position: center left;
}

