// ---    Business story     ---  //

.business-story {
  font-size: 1rem;
  position: relative;
  margin-bottom: 12.8em;

  &::before {
    content: '';
    position: absolute;
    bottom: -15.5em;
    left: 3em;
    height: 39.2em;
    width: 52.1em;
    background-image: url(../images/business/bg__text--business-story.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    z-index: -1;
  }

  &__box {
    display: flex;
  }

  &__title {
    text-align: left;
    margin-top: 0;
    margin-bottom: 0.98em;
  }

  &__left {
    margin-right: 3em;
  }

  &__photo-top {
    width: 55.8em;
    margin-bottom: 2em;

    .img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  &__photo-bottom {
    display: flex;
    width: 100%;
    height: 33.8em;

    .img {
      width: 26.9em;

      &:first-child {
        margin-right: 2em;
      }
    }
  }

  &__text {
    font-size: $rc-font-size-h5;
    line-height: 1.46em;
    margin-top: 0;
    margin-bottom: 0.8em;

    &:nth-child(2) {
      margin-top: 0;
    }

    .list {
      list-style: none;
      padding-left: 1em;

      .item {
        position: relative;
        margin-bottom: 0.3em;

        &::before {
          content: "";
          position: absolute;
          left: -1.1em;
          color: $rc-accent;
        }
      }
    }
  }
}
