// ---    News     ---  //

.home-news {
  position: relative;
  font-size: 1rem;
  margin-bottom: 13em;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 16.6em;
    width: 55.2em;
    background-image: url(../images/bg__text--home-news.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    z-index: 0;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 2.94em;
  }

  .article {
    position: relative;
    display: flex;

    &__photo {
      display: block;
      background-image: url(../images/photo__news.jpg);
      background-repeat: no-repeat;
      background-position: left top;
      background-size: 70em 56em;
      width: 100%;
      height: auto;
      padding: 9em 0 5em 0;
    }


    &__text-block {
      position: relative;
      width: 67.6em;
      background: $rc-gradient-button;
      padding: 4.5em 5.5em;
      margin-right: 0;
      margin-left: auto;
      margin-bottom: 10em;

      .title {
        font-size: $rc-font-size-h3;
        font-weight: 400;
        line-height: 1em;
        margin-top: 0;
        margin-bottom: 0.8em;

        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 3.2em;
          width: 7.2em;
          height: 0.08em;
          background-color: $rc-bg--black;
        }
      }

      .text {
        display: block;
        font-size: $rc-font-size-h5;
        line-height: 1.2;
        margin-top: 0;
        margin-bottom: 0;
      }
    }

    &__thumbnails {
      display: inline-flex;

      .img-thumbnails {
        width: 21.3em;
        height: 19.1em;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        margin-right: 3.5em;

        &:nth-child(1) {
          background-image: url(../images/photo__news--mini-1.jpg);
        }

        &:nth-child(2) {
          background-image: url(../images/photo__news--mini-2.jpg);
        }

        &:nth-child(3) {
          background-image: url(../images/photo__news--mini-3.jpg);
          margin-right: 0;
        }
      }
    }
  }
}
