// ---    Business technobase     ---  //

.business-technobase {
  font-size: 1rem;
  position: relative;
  margin-bottom: 13em;

  &::before {
    content: '';
    position: absolute;
    top: -3.5em;
    left: 0;
    height: 27.5em;
    width: 80.2em;
    background-image: url(../images/business/bg__text--business-technobase.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    z-index: -1;
  }

  .technobase-items {
    display: flex;
    background-image: url(../images/business/photo__business--tehnical.jpg);
    background-repeat: no-repeat;
    background-position: left top;
    background-size: contain;
    width: 100%;
    height: auto;
    padding: 1.6em 0;

    &__list {
      list-style: none;
      padding: 0;
      margin: 0 0 0 auto;
    }

    &__item {
      display: flex;
      align-items: center;
      width: 65.5em;
      background-color: rgba($color: #111111, $alpha: 0.9);
      padding: 3em 2.5em;
      margin-bottom: 2em;

      &:last-child {
        margin-bottom: 0;
      }
    }

    .icon-business {
      width: 7.7em;
      height: 7.7em;
      min-width: 7.7em;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      margin-right: 3em;
    }

    .icon-business--technical-1 {
      background-image: url(../images/icons/business/icon__business--technical-1.png);
    }

    .icon-business--technical-2 {
      background-image: url(../images/icons/business/icon__business--technical-2.png);
    }

    .icon-business--technical-3 {
      background-image: url(../images/icons/business/icon__business--technical-3.png);
    }

    .icon-business--technical-4 {
      background-image: url(../images/icons/business/icon__business--technical-4.png);
    }

    .icon-business--technical-5 {
      background-image: url(../images/icons/business/icon__business--technical-5.png);
      min-width: 9.4em;
      margin-right: 2em;
    }

    &__text {
      font-family: $rc-font-family-accent;
      font-size: $rc-font-size-h6;
      line-height: 1.42em;
    }
  }

  &__footnote {
    text-align: center;
    font-size: 1.7rem;
    background: $rc-gradient-line;
    padding: 1.47em 0;
    margin-top: 1.76em;
    margin-bottom: 0;
  }
}
