// ---    Footer     ---  //

.footer {
  font-size: 1rem;
  margin-bottom: 7.5em;

  &__box {
    display: flex;
    align-items: center;
  }

  &__logo {
    position: relative;
  }

  &__menu {
    margin-left: 13.4em;

    .menu-top {

      &__item {
        margin-left: 3em;
      }

      &__link {
        font-size: 1.23rem;
        letter-spacing: .083em;
        color: $rc-text--gray;
      }
    }
  }

  &__copy {
    margin-right: 0;
    margin-left: auto;

    .text {
      font-size: 1.23rem;
      color: $rc-text--gray;
    }
  }
}
