// Mobile first
$rc-mediaWidthMobile: 479px;
$rc-mediaWidthTablet: 767px;
$rc-mediaWidthTabletHorizontal: 1023px;
$rc-mediaWidthLaptop: 1279px;
$rc-mediaWidthDesctop: 1439px;

// USER VARIABLES SECTION

//-- Color ------

$rc-accent: #a594c9;
$rc-accen--dark: #8c7faa;
$rc-text--black: #272727;
$rc-bg--black: #2b2b2b;
$rc-black: #111111;
$rc-run---stroke: #5ad1f8;
$rc-border--input: #878b8d;
$rc-bg--input: #323232;
$rc-color--input: #bfbfbf;
$rc-border--button: #eaeced;
$rc-text--gray: #7a7a7a;
$rc-table--line: #222222;
$rc-table--border: #3a3a3a;
$rc-white: #ffffff;

$rc-dark-red: #c02e3b;

$rc-gradient-button: linear-gradient( 125deg, rgb(64,154,111) 0%, rgb(62,113,144) 28%, rgb(61,85,150) 56%, rgb(89,83,147) 83%, rgb(113,86,158) 100%);
$rc-gradient-line: linear-gradient(138deg, rgba(61,109,147,1) 0%, rgba(78,88,162,1) 50%, rgba(108,87,159,1) 100%);


//-- Font size ------

$rc-font-size-default: 1.6rem;

$rc-font-size-h1: 6.2rem;
$rc-font-size-h2: 5.1rem;
$rc-font-size-h3: 2.5rem;
$rc-font-size-h4: 1.8rem;
$rc-font-size-h5: 1.6rem;
$rc-font-size-h6: 1.4rem;

$rc-font-size-phone: 3rem;
$rc-font-size-menu: 2.3rem;
$rc-font-size-input: 2rem;
$rc-font-size-footer: 1.25rem;

//-- Line height ------

$rc-line-height-h1: 7.5rem;
$rc-line-height-h2: 5.6rem;
$rc-line-height-h3: 2.8rem;
$rc-line-height-h4: 2.2rem;
$rc-line-height-h5: 2rem;
$rc-line-height-h6: 1.5rem;

//-- Font weight ------
$rc-font-weight-think: 100;
$rc-font-weight-light: 300;
$rc-font-weight-regular: 400;
$rc-font-weight-medium: 500;
$rc-font-weight-bold: 700;

//-- Font family ------

$rc-system: -apple-system, blinkmacsystemfont, arial, sans-serif; // System Font Family
$rc-font-family-accent: avantgardectt, sans-serif; // AvantGardeCTT Font Family
$rc-font-family-default: avantgardegothicc, sans-serif; // AvantGardeGothicC Font Family
$rc-font-family-title: bebasneue, sans-serif; // BebasNeue Font Family

//-- Radio Check ------

$rc-chbx_size: 1.6rem;
$rc-blue: #c02e3b;
$rc-teal: #009688;
$rc-orange: #ff9800;
$rc-light-grey: #f1f1f1;

$rc-tt: 0.2s;
$rc-tt_toggle: 0.4s;
