.form {

  .input,
  .textarea {
    display: flex;
    align-items: center;
    font-size: 2rem;
    color: $rc-white;
    border: 0;
    border-radius: 0.3em;
    background-color: $rc-bg--input;
    width: 18.5em;
    height: 3.95em;
    padding: 0 1.5em;

    &::placeholder {
      transform: translateY(0);
      font-size: 2rem;
      color: $rc-white;
      transition: all 0.3s ease-in-out;
    }

    &:focus {

      &::placeholder {
        transform: translateY(-10em);
        transition: all 0.6s ease-in-out;
      }
    }
  }

  .textarea {
    height: 8.3em;
    padding-top: 1.5em;
  }
}
